<template>
    <div>
      <nav-header></nav-header>
      <div class="container">
        <div class="page-title-normal">
          <h2 class="page-title-h2"><span>订单确认</span></h2>
        </div>
        <!-- 进度条 -->
        <div class="check-step">
          <ul>
            <li class="cur"><span>确认</span> 地址</li>
            <li class="cur"><span>预览</span> 订单</li>
            <li  class="cur"><span>开始</span> 支付</li>
            <li  class="cur"><span>订单</span> 确认</li>
          </ul>
        </div>

        <div class="order-create">
          <div class="order-create-pic"><img src="/static/ok-2.png" alt=""></div>
          <div class="order-create-main">
            <h3>恭喜你! <br>订单生成成功</h3>
            <p>
              <span>订单ID：{{orderId}}</span>
              <span>实付金额：{{orderTotal|currency('$')}}</span>
            </p>
            <div class="order-create-btn-wrap">
              <div class="btn-l-wrap">
                <router-link class="btn btn--m" to="/cart">返回购物车t</router-link>
              </div>
              <div class="btn-r-wrap">
                <router-link class="btn btn--m" to="/">返回商品列表</router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Modal :mdShow="modalConfirm" @close="closeModal">
        <p slot="message">成功扣款{{orderTotal}}元</p>
        <div slot="btnGroup">
          <a class="btn btn--m btn--red" href="javascript:;" @click="modalConfirm = false">确定</a>
        </div>
      </Modal>
      <nav-footer></nav-footer>
    </div>
</template>
<script>
    import NavHeader from './../components/NavHeader'
    import NavFooter from './../components/NavFooter'
    import NavBread from './../components/NavBread'
    import {currency} from './../util/currency'
    import axios from 'axios'
    import Modal from './../components/Modal'
    export default{
        data(){
            return{
                orderId:'',
                orderTotal:0,
              modalConfirm:false
            }
        },
        components:{
          NavHeader,
          NavFooter,
          NavBread,
          Modal
        },
        filters:{
          currency:currency
        },
        mounted(){
          this.modalConfirm=true;
            var orderId = this.$route.query.orderId;
          var money = this.$route.query.money;
          this.orderId = orderId;
          this.orderTotal=money;
            console.log("orderId:"+orderId);
            if(!orderId){
              return;
            }

        }
    }
</script>



