      </defs>
    </svg>
    <div class="container">
      <div class="checkout-order">
        <!-- process step -->
        <div class="check-step">
          <ul>
            <li class="cur"><span>确认</span> 地址</li>
            <li class="cur"><span>预览</span> 订单</li>
            <li><span>开始</span> 支付</li>
            <li><span>订单</span> 确认</li>
          </ul>
        </div>

        <!-- order list -->
        <div class="page-title-normal checkout-title">
          <h2><span>订单内容</span></h2>
        </div>
        <div class="item-list-wrap confirm-item-list-wrap">
          <div class="cart-item order-item">
            <div class="cart-item-head">
              <ul>
                <li>商品</li>
                <li>单价</li>
                <li>数量</li>
                <li>金额</li>
              </ul>
            </div>
            <ul class="cart-item-list">
              <li v-for="item in cartList" >
                <div class="cart-tab-1">
                  <div class="cart-item-pic">
                    <img v-lazy="'/static/'+item.img" :alt="item.goodName">
                  </div>
                  <div class="cart-item-title">
                    <div class="item-name">{{item.goodName}}</div>

                  </div>
                </div>
                <div class="cart-tab-2">
                  <div class="item-price">{{item.price|currency('$')}}</div>
                </div>
                <div class="cart-tab-3">
                  <div class="item-quantity">
                    <div class="select-self">
                      <div class="select-self-area">
                       <a class="input-sub" @click="editCart('minu',item)">-</a>
                          <span class="select-ipt">×{{item.total}}</span>
                        <a class="input-add" @click="editCart('add',item)">+</a>
                      </div>
                    </div>
                    <div class="item-stock item-stock-no"></div>
                  </div>
                </div>
                <div class="cart-tab-4">
                  <div class="item-price-total">{{(item.price*item.total)|currency('$')}}</div>
                </div>
              </li>
            </ul>
          </div>
        </div>

        <!-- Price count -->
        <div class="price-count-wrap">
          <div class="price-count">
            <ul>
              <li>
                <span>商品总金额:</span>
                <span>{{subTotal|currency('$')}}</span>
              </li>
              <li>
                <span>快递费:</span>
                <span>{{shipping|currency('$')}}</span>
              </li>


              <li class="order-total-price">
                <span>总支付:</span>
                <span>{{orderTotal|currency('$')}}</span>
              </li>
            </ul>
          </div>
        </div>

        <div class="order-foot-wrap">
          <div class="prev-btn-wrap">
            <router-link class="btn btn--m" to="/address">后退</router-link>
          </div>
          <div class="next-btn-wrap">
            <button class="btn btn--m btn--red" @click="payMent">支付</button>
          </div>
        </div>
      </div>
</div>
    <Modal :mdShow="modalConfirm" @close="closeModal">
      <p slot="message">成功扣款{{orderTotal}}元</p>
      <div slot="btnGroup">
       <a class="btn btn--m btn--red" href="javascript:;" @click="modalConfirm = false">关闭</a>
      </div>
    </Modal>
    <nav-footer></nav-footer>
  </div>
</template>
<script>
  import Modal from './../components/Modal'
  import NavHeader from './../components/NavHeader'
  import NavFooter from './../components/NavFooter'
  import NavBread from './../components/NavBread'
  import {currency} from './../util/currency'
  import axios from 'axios'
  import { mapState } from 'vuex'


  export default{
      data(){
          return{
              shipping:2.0,
              discount:200,
              tax:400,
              subTotal:0,
              orderTotal:0,
              cartList:[],
            modalConfirm:false

          }
      },
    computed:{
      ...mapState(['nickName','cartCount','goodIds'])
    },
    /*nickName(){
      return this.$store.state.nickName;
    },
    cartCount(){
      return this.$store.state.cartCount;
    }*/
    getCarListArry(){
      return this.$store.state.goodIds;
    },
      mounted(){

          this.init();
      },
      components:{
        NavHeader,
        NavFooter,
        NavBread,
        Modal
      },
      filters:{
        currency:currency
      },

      methods:{
          init(){
            axios.get("/goods/cartList?goodIds="+this.$store.state.goodIds).then((response)=>{
              let res = response.data;
              this.cartList = res.datalist;
              this.cartList.forEach((item)=>{
                  this.subTotal += item.price*item.total;
               });
              this.orderTotal = this.subTotal+this.shipping;
          });
         },
          payMent(){
          //  this.modalConfirm=true;
            var postDataList=[];
            this.cartList.forEach((item)=>{
              var jsonData={};
              jsonData.goodId=item.goodId;
              jsonData.goodsNumber=item.total;
              postDataList.push(jsonData);
            });

             // var addressId = this.$route.query.addressId;
              axios.post("/order/payMent",{
               // addressId:addressId,
                postDataList:postDataList,
                total:this.orderTotal
              }).then((response)=>{
                  let res = response.data;
                  if(res.status=="0"){
                 // loginModalFlag=true;
                     this.$router.push({
                          path:'/orderSuccess?orderId='+res.data.orderId+'&money='+res.data.money
                      })
                  }
              })
          },
        editCart(flag,item){
          if(flag=='add'){
            item.total++;
          }else if(flag=='minu'){
            if(item.total<=1){
              return;
            }
            item.total--;
          }
        // this.orderTotal = this.subTotal+this.shipping;
          this.subTotal=0;
          this.cartList.forEach((item)=>{
             this.subTotal += item.price*item.total;
          });
          this.orderTotal = this.subTotal+this.shipping;
        },
      }
  }
</script>



